<template>
    <div class="animated fadeIn">
        <b-row>
            <b-col md="12">
                <b-card header-tag="header" footer-tag="footer">
                    <div slot="header">
                        <div class="d-flex w-100 justify-content-between">
                            <div>
                                <i class="fa fa-align-justify"></i>
                                <strong>&nbsp;Почта</strong>
                            </div>
                        </div>
                    </div>
                    <b-list-group>
                        <b-list-group-item
                            class="flex-column align-items-start"
                            v-for="(l, k, i) in dataSlice"
                            :key="i"
                        >
                            <div class="d-flex w-100 justify-content-between">
                                <h5 class="mb-1">{{l.name}}</h5>
                                <div class="d-flex w-80 justify-content-between">
                                    <small>
                                        {{ new Date(l.date).toLocaleDateString() }}
                                        {{ ("0" + new Date(l.date).getHours()).slice(-2) + ':' + ("0" + new Date(l.date).getMinutes()).slice(-2) }}
                                    </small>
                                </div>
                            </div>
                            <small class="info mb-2">{{l.email}}</small>
                            <small class="info mb-2">{{l.phone}}</small>
                            <p class="mb-1" style="margin-top:10px;">{{l.message}}</p>
                        </b-list-group-item>
                    </b-list-group>
                </b-card>
            </b-col>
        </b-row>

        <b-pagination
            size="md"
            v-if="data"
            :key="key"
            :total-rows="data.length"
            v-model="currentPage"
            :per-page="perPage"
        ></b-pagination>
        <br>
    </div>
</template>

<script>
import FormDataCard from "./components/FormDataCard.vue";
import AppData from "../AppData.js";
import firebase from "../firebase.js";
export default {
    name: "Mail",
    data() {
        return {
            key: 0,
            data: undefined,
            currentPage: 1,
            perPage: 10,
            last: undefined,
            first: undefined
        };
    },
    watch: {
        currentPage(val, old) {}
    },
    computed: {
        dataSlice() {
            if (this.data === undefined) {
                return [];
            } else {
                let start = (this.currentPage - 1) * this.perPage;
                console.log(this.data.slice(start, start + this.perPage));
                return this.data.slice(start, start + this.perPage);
            }
        }
    },
    methods: {
        clickNext() {},
        clickPrev() {}
    },
    mounted() {
        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                AppData.fetchData("mail", true, { sort: "date", order: "desc" })
                    .then(result => {
                        this.data = result;
                        console.log(result);
                    })
                    .catch(err => {
                        console.log(err);
                    });
            } else {
                this.$router.push("/pages/login/");
                // Implement logic to trigger the login dialog here or redirect to sign-in page.
                // e.g. showDialog()
            }
        });
    },
    components: {
        FormDataCard
    }
};
</script>

